#articleDiv{
    -ms-flex: 1 1;
        flex: 1 1;
    color: rgb(18, 123, 150);
    background-color: rgb(255, 255, 255);
    font-size: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 50vw;
    -ms-flex-align: center;
        align-items: center;
    border: solid 1px rgb(199, 198, 198);
    padding: 10px;
    max-height: 50vh;
    overflow: auto;
}

#articleDiv header{
    -ms-flex: 1 1;
        flex: 1 1;
   
}




.snippet:empty{
    display: none;
}

.articleDiv button{
  width: 100px;
  margin: auto;
  background-color: salmon;
}

.title:hover{
    color: blue;
}
.title:visited{
    color: purple;
}
.title:active{
    color: red;
}

#articles{
    background-color: #161e3acf;
}






